body {
  background-color: #fff;
  font-family: 'Kanit', sans-serif;
  font-size: 16px;
  line-height: 1.6em;
  color: #000;
}

input, textarea, select {
  font-family: 'Kanit', sans-serif;
  font-size: 16px;
  line-height: 1.6em;
  color: #000;
}

div.bged, section.bged, article.bged {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

div.bged::before, section.bged::before, article.bged::before {
  content: '';
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 1;
}

div.bged.bgcolor_transparent::before, section.bged.bgcolor_transparent::before, article.bged.bgcolor_transparent::before {
  content: none;
}

div.bged.bgcolor1::before, section.bged.bgcolor1::before, article.bged.bgcolor1::before {
  background: rgba(0, 0, 0, 0.6);
}

div.bged.circle::before, section.bged.circle::before, article.bged.circle::before {
  border-radius: 50%;
}

img.lazy {
  background-color: transparent;
  display: none;
}

/* -------------------------------------------------------- HEADER -------------------------------------------------------- */

header {
  z-index: 10;
}

.alert-success, .alert-warning, .alert-danger {
  background-image: none;
  border-radius: 0;
}

/* ---------------------------- NAVBAR ---------------------------- */

nav.navbar {
  /*box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);*/
  box-shadow: none;
  background: transparent;
  border: none;
  transition: ease-in-out 0.2s;
}

.navbar-default {
  .navbar-collapse, .navbar-form {
    border: none;
  }
  &.background {
    .navbar-collapse, .navbar-form {
      border-top: solid 1px #e7e7e7;
    }
  }
}

.nav > li > a {
  padding: 10px 20px;
}

.navbar-nav {
  margin: 0;
  &.background {
    margin: 7.5px -15px;
  }
}

.navbar-brand {
  padding: 5px;
  > img {
    max-height: 40px;
  }
}

@media (min-width: 992px) {
  nav.navbar.background.shrinked {
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
  }
}

@media (max-width: 991px) {
  nav.navbar.background {
    /*box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);*/
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
  }
  .navbar-brand > img {
    max-height: 30px;
  }
  .navbar-right {
    float: left !important;
    margin-right: 0;
  }
}

/* a text */

.navbar-default {
  .navbar-nav > li > a {
    color: #fff;
    font-weight: 700;
    text-shadow: none;
    transition: ease-in-out 0.2s;
    &:focus {
      color: #fff;
      font-weight: 700;
      text-shadow: none;
      transition: ease-in-out 0.2s;
    }
  }
  &.background .navbar-nav > li > a {
    color: #212121;
    &:focus {
      color: #212121;
    }
  }
  .navbar-nav {
    li:hover > a {
      color: #fff;
    }
    a {
      &:focus, &:hover {
        color: #fff;
        background: #ff9c10;
      }
    }
    > {
      .active > a {
        color: #fff;
        background: #ff9c10;
        &:focus, &:hover {
          color: #fff;
          background: #ff9c10;
        }
      }
      .open > a {
        color: #fff;
        background: #ff9c10;
        &:focus, &:hover {
          color: #fff;
          background: #ff9c10;
        }
      }
    }
  }
}

/* li bg */

.navbar-nav > li {
  background-color: #3F88C5;
  transition: ease-in-out 0.2s;
  &.color_1 {
    background-color: #4CB944;
  }
  &.color_2 {
    background-color: #F06543;
  }
  &.color_3 {
    background-color: #042A2B;
  }
  &.color_4 {
    background-color: #2F4858;
  }
  &.color_5 {
    background-color: #FF206E;
  }
  &.color_6 {
    background-color: #4B4E6D;
  }
  &.color_7 {
    background-color: #B6174B;
  }
  &.active {
    background-color: #ff9c10;
  }
}

/* backgrounds */

/* -- */

.navbar-default .navbar-nav > {
  li:hover {
    background-color: #ff9c10;
  }
  .active > a, .open > a {
    background-image: none;
    background-color: #ff9c10;
    box-shadow: none;
    color: #fff;
  }
}

/* dropdown */

.dropdown-menu > li {
  > a:focus, &:hover {
    background-color: #ff9c10;
    background-image: none;
  }
  > a {
    &:focus, &:hover {
      background-color: transparent;
      background-image: none;
    }
  }
}

.navbar-default .navbar-nav > .open {
  background-color: #ff9c10;
  > a {
    color: #fff;
    background-color: transparent;
    &:focus, &:hover {
      color: #fff;
      background-color: transparent;
    }
  }
}

.navbar-nav .open .dropdown-menu {
  padding: 0;
  background-color: #fff;
  border: none;
  border-radius: 0;
}

.dropdown-header, .dropdown-menu > li > a {
  padding: 8px 20px;
}

/* flags */

.flag-icons img, .nav li.flag-icons > a > img {
  max-width: 24px;
}

/* mobile */

@media (min-width: 992px) {
  .navbar-nav .navbar-default .navbar-nav > li a {
    padding-left: 1em;
    padding-right: 1em;
  }
  nav.navbar {
    &.navbar-default .navbar-nav {
      > li > a {
        color: #fff;
      }
      li {
        &:hover > a, &:active > a {
          color: #fff;
        }
      }
    }
    &.background.shrinked.navbar-default .navbar-nav > li a {
      color: #212121;
    }
    &.navbar-default .navbar-nav > li {
      &:hover > a, &.active > a, &.open > a {
        padding-bottom: 25px;
        color: #fff;
      }
    }
    &.background.shrinked.navbar-default .navbar-nav {
      > li {
        &:hover > a, &.active > a {
          padding-bottom: 25px;
          color: #fff;
        }
      }
      li.open {
        > a, .dropdown-menu li:hover a {
          padding-bottom: 25px;
          color: #fff;
        }
      }
    }
  }
  /* backgrounds */
  /*
  nav.navbar.navbar-default .navbar-nav > li.color_7 > a {
      color: #212121;
  }
  */
  /* -- */
}

@media (max-width: 767px) {
  .navbar-default {
    &.background .navbar-nav .open .dropdown-menu > li > a {
      color: #212121;
    }
    .navbar-nav .open .dropdown-menu > li > a {
      &:focus, &:hover {
        color: #fff;
        background-color: #ff9c10;
      }
    }
  }
}

