/* ---------------------------- Full-screen ---------------------------- */

div.full-height, section.full-height, article.full-height {
  min-height: 100%;
  margin: 0;
}

div.height-100, section.height-100, article.height-100 {
  height: 100%;
}

@media (max-width: 991px) {
  div.full-height, section.full-height, article.full-height {
    min-height: 400px;
  }
}

/* ---------------------------- /Full-screen ---------------------------- */

div.lazy, section.lazy, article.lazy {
  background-color: #434343 !important;
}

/* /////// */

div.zebra, section.zebra, article.zebra {
  background-color: #fff;
  color: #000;
}

div.zebra:nth-child(odd), section.zebra:nth-child(odd), article.zebra:nth-child(odd) {
  background-color: #163A51;
  color: #fff;
}

div.zebra:nth-child(odd) {
  h1, h2, h3, h4, p {
    color: #fff;
  }
}

section.zebra:nth-child(odd) {
  h1, h2, h3, h4, p {
    color: #fff;
  }
}

article.zebra:nth-child(odd) {
  h1, h2, h3, h4, p {
    color: #fff;
  }
}

div.page, section.page, article.page {
  padding: 70px 0;
}

div.page.no-padding, section.page.no-padding, article.page.no-padding {
  padding: 0;
}

/* carousel */

.carousel {
  margin-top: -70px;
}

.slick-dotted.slick-slider {
  margin-bottom: 0;
}

.carousel .item {
  height: 600px;
  &.slide {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
  }
  .slide_caption {
    position: absolute;
    bottom: 10%;
    width: 100%;
    z-index: 2;
    p {
      font-size: 40px;
      text-align: center;
      color: #fff;
    }
  }
}

.slick-dots {
  bottom: 16px;
  z-index: 2;
  li {
    &.slick-active button:before {
      opacity: .75;
      color: #ff9c10;
    }
    button:before {
      color: #fff;
    }
  }
}

/* images */

.carousel .item.slide {
  &.slide1 {
    background-image: url(../images/banners/bg1.jpg);
  }
  &.slide2 {
    background-image: url(../images/banners/bg2.jpg);
  }
  &.slide3 {
    background-image: url(../images/banners/bg3.jpg);
  }
}

/* /images */

/* carousel */

div.row > {
  h1, h2, h3, h4, p {
    padding: 0 15px;
  }
}

h1 {
  font-size: 275%;
  color: #212121;
}

h2 {
  font-size: 175%;
}

h3 {
  font-size: 150%;
}

p {
  font-size: 115%;
  color: #212121;
  font-weight: 300;
  &.line-height-150 {
    line-height: 150%;
  }
}

.wrapper .container h1 {
  margin-bottom: 50px;
}

span {
  &.highlight {
    font-size: 125%;
    text-transform: uppercase;
    font-weight: bold;
  }
  &.colored {
    color: #163A51;
  }
  &.colored2 {
    color: #22AED1;
  }
  &.colored3 {
    color: #ff9c10;
  }
}

h2.highlight {
  padding: 20px 5px;
  text-transform: uppercase;
  font-size: 275%;
}

@media (max-width: 991px) {
  h2.highlight {
    font-size: 175%;
  }
}

a.stretch {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

/* Flippers */

#page_cards {
  div {
    padding: 0;
  }
  .row {
    margin: 0;
  }
}

.tile-container {
  margin-top: 15px;
  .flip-container {
    max-width: 150px;
    margin-left: auto;
    margin-right: auto;
  }
}

.flipper {
  .front > a, .back > a {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  background-size: cover;
  img {
    width: 100%;
    max-height: 300px;
  }
}

.flipper-height {
  height: 480px;
}

.flipper {
  p {
    color: #fff;
  }
  &.color_1 {
    background: #ff9c10;
    p {
      color: #000;
    }
  }
  &.color_2 {
    background: #000;
  }
  &.color_3 {
    background: #ddd;
    p {
      color: #000;
    }
  }
}

/* Iterators */

.iterator > div {
  background-color: #eee;
  width: 100%;
  height: 200px;
  transition: ease-in-out 0.3s;
  &.hover {
    background-color: #ff9c10;
  }
}

/* /Iterators */

/* Buttons */

.button.sliding_bg {
  color: #31302B;
  background: #FFF;
  padding: 12px 17px;
  margin: 25px;
  border: 3px solid #31302B;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 2px;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 #31302B;
  -webkit-transition: all ease 0.8s;
  -moz-transition: all ease 0.8s;
  transition: all ease 0.8s;
  &:hover {
    box-shadow: inset 0 100px 0 0 #31302B;
    color: #FFF;
  }
}

/* /Buttons */

/* Accordion */

