  margin-top: 30px;
  p {
    color: #000;
  }
}

.accordion-item {
  position: relative;
  a {
    color: inherit;
    text-decoration: none;
    &:focus {
      color: inherit;
      text-decoration: none;
    }
  }
}

.accordion-container .accordion-item .accordion-item-content p {
